import express from 'express';
import getRoles from '../controllers/roles_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /auth/verifyOtp:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns a jwt token if login is successful
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required credentials"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              mobileNumber:
 *                  type: string
 *              code:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              jwt:
 *                  type: string
 *              refreshToken:
 *                  type: string
 *       400:
 *         description: Returns 400 if userId or password are invalid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.get(`${baseUrl}/`, getRoles);

export default router;
